import React from 'react'
import {NavLink} from 'react-router-dom'


export default function AppIcon(props) {

	const {enName, name, img} = props

	return (
		<>
			<button style={{border: 'solid 1px #000', borderRadius: '0.5em'}}
			        onClick={() => alert(name)}
			>
				<img src={img} style={{width: '4em'}} />
				{name}
			</button>
			<NavLink to={`/meizu/run/${enName}`}>{name}</NavLink>
		</>
	)
}