import Desktop1 from './Desktop1'
import appData from '../datasource/application-data.json'


export default function Desktop0() {
	return (
		<div style={{position: 'relative'}}>

			{/*上*/}
			<div style={{
				position: 'absolute', height: '40vh', left: 0, right: 0
			}}>
				上
			</div>

			{/*中*/}
			<div style={{
				position: 'absolute',
				height: '35vh',
				top: '40vh',
				left: 0,
				right: 0,
				padding: '0 1em'
			}}>
				<Desktop1 data={appData.deskApps} />
			</div>

			{/*下*/}
			<div style={{
				position: 'absolute',
				height: '15vh',
				top: '85vh',
				left: 0,
				right: 0,
				padding: '0 1em'
			}}>
				<Desktop1 data={appData.dockApps} />
			</div>

		</div>
	)
}