import React from 'react'
import List from '@mui/material/List';
import SettingData from '../../datasource/setting-data'
import SettingItem from './SettingItem'
import Enumerable from "linq-js";


/**
 * 设置之网络共享
 */
export default function SettingNetworkShare() {
	console.log(SettingData)

	let data = SettingData.asEnumerable().where(i => i.name === "网络共享").first()
	console.log(data)

	let listData = data.children.asEnumerable().toArray()
	console.log(listData)



	let html = (
		<div id="setting-app">
			<h1 style={{marginBottom: "0.2em"}}>网络共享 </h1>
			<sub style={{display: "inline-block", marginBottom: "1.5em"}}>
				开启热点共享，可再本页面通过 NFC 触碰实现免密共享网络
			</sub>
			<List>
				{
					listData.map((one, i) => (
						<SettingItem data={one} index={i} key={`setting-item-${i}`} />
					))
				}
			</List>
		</div>
	)


	return html
}



