import React from 'react'
import List from '@mui/material/List';
import SettingData from '../../datasource/setting-data'
import SettingItem from './SettingItem'
import Enumerable from "linq-js";


/**
 * 设置之运营商网络
 */
export default function settingMobileNetwork() {
	console.log(SettingData)

	let data = SettingData.asEnumerable().where(i => i.name === "运营商网络").first()
	console.log(data)

	let listData = data.children.asEnumerable().toArray()
	console.log(listData)


	let html = (
		<div id="setting-app">
			<h1>运营商网络</h1>
			<List>
				{
					listData.map((one, i) => (
						<SettingItem data={one} index={i} key={`setting-item-${i}`} />
					))
				}
			</List>
		</div>
	)


	return html
}