import React from 'react'
import SettingData from '../../datasource/setting-data'
import {Grid} from "@mui/material";
import Enumerable from "linq-js";


/**
 * 设置之我的手机
 */
export default function SettingMyPhone() {
	console.log(SettingData)

	let data = SettingData.asEnumerable().where(i => i.name === "我的手机").first()
	console.log(data)

	let listData1 = data.children.asEnumerable().where(i => i.type !== 2).toArray()
	console.log(listData1)

	let listData2 = data.children.asEnumerable().where(i => i.type === 2).toArray()
	console.log(listData2)

	let html = (
		<div id="setting-app">
			<h1>MEIZU 18</h1>

			<Grid container rowSpacing={5}>
				{
					listData1.map(one => (
						<Child1 data={one} />
					))
				}

				{
					listData2.map(one => (
						<Child2 data={one} />
					))
				}
			</Grid>

		</div>
	)


	return html
}



function Child1(props) {
	var one = props.data

	let h5
	if (one.clickTo === undefined) {
		h5 = (<h5>{one.name}</h5>)
	} else {
		h5 = (<h5>{one.name} <i className="fa fa-angle-right" /></h5>)
	}


	if (one.name === undefined) {
		return (
			<Grid item xs={12}>
				<hr />
			</Grid>
		)
	} else {
		return (
			<Grid item xs={6}>
				<i className={`fa ${one.icon}`} />
				{h5}
				<h3>
					{one.value}
					{
						one.valueDesc !== undefined &&
						<label className="value-desc">{one.valueDesc}</label>
					}
				</h3>
			</Grid>
		)
	}
}


function Child2(props) {
	var one = props.data

	if (one.name === undefined) {
		return (
			<Grid item xs={12}>
				<hr />
			</Grid>
		)
	} else {
		return (
			<Grid item xs={6}>
				<i className={`fa ${one.icon}`} />
				<h3>{one.name}</h3>
				<h5>{one.value}</h5>
			</Grid>
		)
	}
}