import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import SettingHome from './component/setting/SettingHome'
import SettingMyPhone from './component/setting/SettingMyPhone'
import SettingMobileNetwork from './component/setting/SettingMobileNetwork'
import SettingWifi from './component/setting/SettingWifi'
import SettingNetworkShare from './component/setting/SettingNetworkShare'
import SettingBluetooth from './component/setting/SettingBluetooth'
// import reportWebVitals from './reportWebVitals';
import {BrowserRouter, Route, Routes} from 'react-router-dom';
import ScrollToTop from "./component/ScrollToTop";
import AppContainer from "./component/AppContainer";


ReactDOM.render(
	<React.StrictMode>
		<BrowserRouter>
			<ScrollToTop info="跳转后页面回到顶部，不需要则注释" />
			<Routes>
				<Route path='/meizu' element={<App />} />
				<Route path='/meizu/run' element={<AppContainer />}>
					<Route path={encodeURI('SheZhi')} element={<SettingHome />} />
					<Route path={encodeURI('SheZhi/SettingMyPhone')} element={<SettingMyPhone />} />
					<Route path={encodeURI('SheZhi/SettingMobileNetwork')} element={<SettingMobileNetwork />} />
					<Route path={encodeURI('SheZhi/SettingWifi')} element={<SettingWifi />} />
					<Route path={encodeURI('SheZhi/SettingNetworkShare')} element={<SettingNetworkShare />} />
					<Route path={encodeURI('SheZhi/SettingBluetooth')} element={<SettingBluetooth />} />
					<Route path={encodeURI('SheZhi/:argItemName')} element={
						<h3 style={{color: 'yellow'}}>
							{/*{useParams().argItemName}*/}
						</h3>
					} />
					<Route path="*" element={
						<main style={{padding: "1em", margin: "1em", border: "solid"}}>
							开发中
						</main>
					} />
				</Route>
			</Routes>
		</BrowserRouter>
	</React.StrictMode>
	,
	document.getElementById('root')
);

// // If you want to start measuring performance in your app, pass a function
// // to log results (for example: reportWebVitals(console.log))
